@import "../mixins";
.compare{
  background: #F7F7F7;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  .container{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__grid{
    flex-grow: 1;
    margin-top: 4em;
    display: grid;
    grid-template-columns: 21.375em calc(100% - 21.375em);
  }
  &__left{
    z-index: 3;
    position: relative;
    background-color: #fff;
    &:before,
    &:after{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
    }
    &:before{
      width: 2000px;
      background-color: #fff;
    }
    &:after{
      z-index: 3;
      width: 1.75em;
      background: linear-gradient(270deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0) 100%);
    }
  }
  &__right{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      background: #FFFFFF;
      box-shadow: 0px -1px 0px #EBEBEB;
      top: 0;
      left: 0;
      width: 50000px;
      height: 100%;
    }
  }
  &__top{
    position: relative;
    z-index: 2;
    height: 23em;
    padding: 2.5em 2.5em 0 0;
    .m-check{
      margin-top: 1em;
    }
  }
  &__tabs{
    display: flex;
    flex-wrap: wrap;
    margin-top: .625em;
  }
  &__tab{
    margin: .625em .625em 0 0;
    display: flex;
    align-items: center;
    padding: 0 .9375em;
    height: 2em;
    border-radius: 2em;
    background: #EAEAEA;
    font-weight: 600;
    text-transform: uppercase;
    span{
      font-size: .75em;
    }
    &:hover,
    &._active{
      background: #404040;
      color: #FFFFFF;
    }
  }
  &__rows{
    position: relative;
    z-index: 2;
  }
  &__row{
    display: flex;
    align-items: center;
    font-weight: 600;
    height: 3.75em;
    position: relative;
    &._red-row{
      background: rgba(196, 14, 56, 0.11);
      &:before{
        opacity: 0;
      }
    }
    &:before{
      content: '';
      position: absolute;
      width: 2000px;
      height: 100%;
      right: 0;
      top: 0;
      background: inherit;
    }
    &:nth-child(even){
      background: #F8F8F8;
    }
    span{
      font-size: 1.125em;
      position: relative;
      z-index: 2;
    }
  }
  .swiper{
    &-container{
      position: relative;
      z-index: 2;
    }
  }
}

.compare-card{
  width: 20.625em;
  padding-bottom: 3em;
  &:last-child{
    .compare__row{
      &:before{
        opacity: 1;
        left: 100%;
        right: unset;
      }
    }
  }
  &__top{
    position: relative;
    z-index: 2;
    height: 23em;
    padding: 0 1.875em;
  }
  &__img{
    height: 18.3125em;
    @include center;
    padding: 1em 0 .5em;
    margin-bottom: .5em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    display: block;
    font-weight: 600;
    text-align: center;
    @include truncate(2);
  }
  .compare__row{
    justify-content: center;
    text-align: center;
    &:before{
      left: 0;
      right: unset;
    }
  }
  &__nav{
    width: 14.75em;
    max-width: 100%;
    margin: 1.25em auto 0;
  }
  &__btn{
    width: 100%;
    height: 3.25em;
    font-weight: 700;
    span{
      font-size: .875em;
    }
  }
  &__delete{
    margin-top: .3125em;
    color: $primary;
    &:hover{
      color: $primaryHover;
    }
  }
}


@media screen and (max-width: 992px){
  .compare{
    &__grid{
      padding-top: 7em;
      width: calc(100% + 1.25em);
      margin: .625em 0 0 -.625em;
      position: relative;
      grid-template-columns: 11.875em calc(100% - 11.875em);
    }
    &__left{
      position: relative;
      padding-top: 13.625em;
      &:before{
        display: none;
      }
    }
    &__right{
      background-color: #fff;
      padding: 0 .625em;
    }
    &__top{
      width: 100vw;
      position: absolute;
      left: 0;
      padding: 0 .625em;
      height: 7em;
      bottom: 100%;
      z-index: 4;
    }
    &__row{
      padding: 0 .625em;
      &:before{
        width: calc(100% + 3em);
        right: -3em;
      }
      span{
        z-index: 3;
        font-size: .875em;
      }
    }
    &__subtitle{
      font-size: 1.125em;
      line-height: 189%;
    }
    &__tabs{
      flex-wrap: nowrap;
      @include fullScroll;
    }
    &__tab{
      flex-shrink: 0;
    }
    .swiper{

    }
  }

  .compare-card{
    width: 11em;
    padding-bottom: 1.5em;
    &__top{
      padding: 0 .5em;
      height: 13.625em;
    }
    &__img{
      height: 10.375em;
    }
    &__title{
      font-size: .9375em;
      br{
        display: none;
      }
    }
    .compare__row{
      &:before{
        right: unset !important;
        left: -.625em;
      }
    }
    &__nav{
      width: 8.625em;
    }
    &__btn{
      padding: 0;
    }
    &__delete{
      margin-top: 1em;
    }
  }
}

