@import "mixins";
@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
._backdrop{
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.62);
}

.m-badge{
  border-radius: 2em;
  padding: 0 .625em;
  display: flex;
  align-items: center;
  height: 1.6875em;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  span{
    font-size: .6875em;
  }
  &._red{
    background-color: $primary;
  }
  &._orange{
    background: #E1861A;
  }
  &._blue{
    background: #4DB7F3;
  }
}

.upload-img{
  width: 9.375em;
  height: 9.375em;
  position: relative;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    z-index: 2;
    &:hover{
      &~.upload-img__wrap{
        background-color: $primaryHover;
      }
    }
  }
  &__wrap{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #FFFFFF;
    background-color: $primary;
    backdrop-filter: blur(3.125em);
    border-radius: 1.25em;
    transition: all .3s;
  }
  img{
    width: 3.125em;
    margin-bottom: .625em;
  }
  span{
    font-size: .875em;
    line-height: 157%;
    font-weight: 600;
    letter-spacing: 0.02em;
  }
}

.carousel__slide.has-video{
  &:before{
    content: '';
    @include centerAbsolute;
    width: 2em;
    height: 2em;
    border-radius: 50%;
    background-color: $primary;
    background-image: url(../img/play2.svg);
    @include bgImgSize(80%);
    z-index: 2;
  }
}
