@import "../mixins";
.search{
  padding-bottom: 6.25em;
  background: #F6F6F6;
  flex-grow: 1;
  .compare__tabs{
    margin-top: 1.5em;
  }
  &__grid{
    @include grid100gap(.9375em);
    margin-top: 2.5em;
  }
  &__field{
    margin-top: 2.25em;
    input{
      font-weight: 600;
      font-size: 1em;
      height: 4.25em;
      padding: 0 4em 0 1.875em;
      border-radius: .625em;
      border-color: $primary;
      &:focus{
        border-color: $primaryHover;
      }
    }
    &-btn{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: .625em;
      font-weight: 700;
    }
  }
  .news-card,
  .product-card{
    padding: 1.25em;
    min-height: 15em;
    position: relative;
    background: #FFFFFF;
    box-shadow: 0 .0625em .125em rgba(0, 0, 0, 0.2);
    border-radius: .625em;
    display: flex;
    flex-direction: row;
    justify-content: unset;
    &__img{
      flex-shrink: 0;
      width: 12.5em;
      height: 12.5em;
      padding: 0 !important;
      margin: 0 1.25em 0 0;
    }
    &__content{
      flex-grow: 1;
    }
    &__title{
      span{
        color: $primary;
      }
    }
  }

  .product-card{
    &__title{
      margin: 0;
      text-align: left;
      min-height: unset;
    }
    &__rating{
      justify-content: unset;
    }
    &__props{
      max-width: 36.25em;
      grid-template-columns: 1fr 1fr;
      gap: .625em 2.5em;
    }
    &__nav{
      max-width: 36.25em;
      display: flex;
      align-items: center;
      gap: unset;
    }
    &__btn{
      width: 9.25em;
      flex-shrink: 0;
      margin-right: .625em;
    }
    &__compare{
      padding: 0 .625em;
    }
  }

  .news-card{
    align-items: center;
    &__content{
      margin: 0;
    }
    &__info{
      max-width: 100%;
    }
  }

  .qa-card{
    background: #FFFFFF;
    box-shadow: 0 .0625em .125em rgba(0, 0, 0, 0.2);
    border-radius: .625em;
    padding: 1.5em 1.25em;
    &__title{
      span{
        color: $primary;
      }
    }
  }
}

.search-shop{
  background: #FFFFFF;
  box-shadow: 0 .0625em .125em rgba(0, 0, 0, 0.2);
  border-radius: .625em;
  padding: 1.25em;
  &__address{
    margin-top: .71em;
    span{
      color: $primary;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    margin-top: .625em;
  }
  &__site{
    color: $primary;
  }
  .contact-card__link{
    margin-bottom: 0;
    &:not(:last-child){
      margin-right: 2em;
    }
  }
  &__work{
    font-size: .875em;
    line-height: 160%;
    span{
      font-weight: 500;
    }
  }
}

.download-guide{
  background: #FFFFFF;
  box-shadow: 0 .0625em .125em rgba(0, 0, 0, 0.2);
  border-radius: .625em;
  padding: 1.5em 1.25em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__btn{
    padding: 0 1.875em;
    font-weight: 700;
    margin-left: 1em;
    flex-shrink: 0;
  }
  &__title{
    span{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .search{
    .display1{
      font-size: 1.25em;
      line-height: 130%;
    }
    &__field{
      margin-top: 1.625em;
    }
  }

  ._search-card{
    .product-card{
      &__props{
        grid-template-columns: 100%;
      }
      &__nav{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 1.25em .625em;
      }
      &__btn{
        width: 100%;
      }
      &__compare{
        justify-content: unset;
        padding: 0;
        height: unset;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .search{
    padding-bottom: 2.5em;
    &__grid{
      .product-card{
        display: block;
      }
      .news-card{
        display: block;
        &__img{
          margin-bottom: 1.25em;
        }
        &__title{
          max-width: 15em;
        }
      }
      .qa-card{
        padding: 1.25em;
        &__btn{
          margin-right: .625em;
          width: 2.125em;
          height: 2.125em;
          background-size: .8125em auto;
        }
        &__show{
          width: calc(100% + 1em);
        }
        &__title{
          font-size: 1em;
        }
        &__hidden{
          padding-left: 2.5em;
        }
      }
    }
  }

  ._search-card{
    .product-card{
      &__img{
        height: unset;
        position: static;
        margin: 0 0 1.25em;
        img{
          position: static;
          transform: unset;
        }
        ._view2{
          display: none;
        }
        &:hover{
          ._view1{
            display: none;
          }
          ._view2{
            display: block;
          }
        }
      }
      &__title{
        max-width: 14em;
      }
    }
  }

  .search-shop{
    &__address{
      max-width: 16em;
      line-height: 160%;
    }
    &__nav{
      flex-direction: column;
      align-items: unset;
      margin-top: 1.875em;
    }
    .contact-card__link{
      margin: 0 auto 1.375em 0;
    }
    &__work{
      margin-right: auto;
    }
  }

  .download-guide{
    flex-direction: column;
    padding: 1.25em;
    &__title{
      width: calc(100% + .5em);
      font-size: 1em;
      line-height: 150%;
      margin-right: auto;
    }
    &__btn{
      margin: 1em auto 0 0;
    }
  }
}
