@import "../mixins";
.places-header{
  padding-bottom: 4.125em;
  background: #F7F7F7;
  &__filter{
    margin-top: 2.125em;
  }
  &__active{
    display: none;
  }
  &__links{
    display: flex;
    flex-wrap: wrap;
  }
  &__link{
    margin: .375em .375em 0 0;
    display: flex;
    align-items: center;
    background-color: #EBEBEB;
    border: 1px solid #EBEBEB;
    border-radius: .625em;
    padding: 0 1.875em;
    height: 3.5em;
    font-size: 1em;
    font-weight: 600;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    img{
      width: .875em;
      display: block;
      flex-shrink: 0;
      margin-right: .375em;
    }
    &:hover,
    &._active{
      background-color: #fff;
      color: $black;
    }
  }
}

.places{
  padding: 3.75em 0 7.5em;
  &__content{
    padding-top: 3.25em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.875em));
    gap: 2.5em;
    .place-card{
      &__address{
        margin-top: .57em;
      }
      &__phone{
        margin-top: .75em;
      }
      &__work,
      &__site{
        margin-top: .856em;
      }
    }
  }
}

.places-filter{
  display: grid;
  grid-template-columns: 27.8% 21.8% 21.8% 23.8%;
  justify-content: space-between;
  gap: 1.25em;
  .fg{
    input,
    .nice-select{
      background: #F9F9F9 !important;
      height: 4.25em;
      padding: 0 1.875em;
      border-radius: .625em !important;
    }
    input{
      font-size: 1em;
    }
    .nice-select{
      .current{
        font-size: 1em;
      }
    }

  }
  .tab,
  &__find{
    height: 4.25em;
    padding: 0;
    border-radius: .625em;
    span{
      font-size: 1em;
    }
  }
  &__tabs{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .1875em));
    gap: .375em;
    .tab{
      border: 1px solid #EBEBEB;
      @include center;
      text-transform: uppercase;
      font-weight: 600;
      letter-spacing: 0.04em;
      cursor: pointer;
      transition: all .3s;
      &:hover,
      &._active{
        background: #EBEBEB;
      }
    }
  }
  &__search{

  }
  &__big-search{
    @media screen and (min-width: 993px){
      .search-field{
        grid-column: 1/3;
      }
    }
  }
}

.search-field{
  &._active{
    .search-field__variants{
      opacity: 1;
      pointer-events: unset;
    }
  }
  input{
    padding-right: 3em;
  }
  &__btn{
    position: absolute;
    right: 1.5em;
    top: 50%;
    transform: translateY(-50%);
    svg{
      width: 1.25em;
      height: 1.25em;
    }
  }
  &__variants{
    z-index: 3;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    margin-top: .375em;
    border-radius: .25em;
    padding: .3125em 0;
    border: 1px solid $primary;
    background-color: #fff;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    .option{
      display: flex;
      align-items: center;
      padding: 1.07em;
      font-size: .875em;
      text-transform: uppercase;
      font-weight: 700;
      &:hover{
        color: $primary;
      }
    }
  }
}



@media screen and (max-width: 992px){
  .places-header{
    padding-bottom: .3125em;
    background-color: transparent;
    &__links{
      position: absolute;
      display: block;
      width: 100%;
      left: 0;
      margin-top: .375em;
      border-radius: .25em;
      z-index: 3;
      padding: .3125em;
      border: 1px solid $primary;
      background-color: #fff;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      .places-header__link{
        height: 2.5em;
        margin: 0;
        background-color: transparent;
        border: none;
        border-radius: .5em;
        &._active{
          color: #fff;
          background-color: rgba($primary,.6);
        }
      }
    }
    &__filter{
      margin-top: 1.25em;
      position: relative;
      &._active{
        .places-header__links{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &__active{
      display: block;
      .places-header__link{
        pointer-events: none;
        margin-right: 0;
        padding-right: 1em;
        background: #F9F9F9;
        &:after{
          content: '';
          width: 1.25em;
          height: 1.25em;
          flex-shrink: 0;
          margin-left: auto;
          background-image: url(../img/select-arrow.svg);
          background-size: 100% auto;
          background-repeat: no-repeat;
          background-position: center;
          transition: inherit;
        }
        span{
          padding-right: 1em;
        }
      }
    }
    &__link{
      padding: 0 1.25em;
      span{
        font-size: .75em;
      }
    }
  }

  .places{
    padding: .3125em 0 5em;
    &__content{
      padding-top: 1.25em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1.25em));
    }
  }

  .places-filter{
    grid-template-columns: 100%;
    gap: .625em;
    &__tabs{
      margin-top: .625em;
      order: 2;
    }
    .fg{
      .nice-select{
        height: 3.5em;
        padding: 0 1em 0 1.25em;
        .current{
          font-size: .75em;
        }
        &:before{
          width: 1.25em;
          height: 1.25em;
        }
      }
      input{
        border-radius: .83em !important;
        font-size: .75em;
        padding: 0 1.67em;
        height: 4.66em;
      }
    }
    .tab,
    &__find{
      height: 3em;
      span{
        font-size: .8125em;
      }
    }
  }

  .search-field{
    grid-column: unset;
    &__btn{
      right: 1.25em;
      svg{
        width: 1em;
        height: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .places{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
