@import "../mixins";
.about{
  padding-bottom: 11.25em;
  .video{
    margin-top: 1.875em;
  }
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: auto 19.375em;
    gap: 1.875em;
  }
  .display2{
    font-size: 3.5em;
    line-height: 119%;
  }
}

.video{
  display: block;
  position: relative;
  overflow: hidden;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  video,
  &__img{
    width: 100%;
    display: block;
  }
  video{
    opacity: 0;
  }
  &__play{
    pointer-events: none;
    width: 7.5em;
    height: 7.5em;
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 50%;
    img{
      width: 4.625em;
    }
  }
  &._played{
    .video__play{
      display: none;
    }
    video{
      opacity: 1;
    }
    .video__img{
      opacity: 0;
    }
  }
}

.about-group{
  text-align: right;
  @include grid100gap(.3125em);
  &:not(:last-child){
    margin-bottom: 1.875em;
  }
  &__title{
    font-weight: 600;
    font-size: 3.5em;
  }
  &__desc{
    font-size: .75em;
    line-height: 140%;
  }
}

.groups{
  margin-top: 3.125em;
  &__links{
    display: flex;
    align-items: center;
    margin-top: 1.25em;
  }
  &__link{
    font-weight: 700;
    font-size: 1.5em;
    line-height: 267%;
    letter-spacing: 0.02em;
    color: $primary;
    &:hover{
      color: $primaryHover;
    }
    &:not(:last-child){
      margin-right: 3.5em;
    }
  }
}

.vacancies{
  margin-top: 3.125em;
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.25em 4em;
  }
  &__subtitle{
    font-weight: 700;
    font-size: 1.1875em;
    line-height: 179%;
    letter-spacing: 0.02em;
  }
  &__desc{
    font-size: .875em;
    line-height: 160%;
  }
}

.purchase{
  margin-top: 3.75em;
  &__grid{
    margin-top: 1.875em;
    @include grid100gap(1.875em);
  }
}

.purchase-card{
  display: grid;
  grid-template-columns: auto 12.5em;
  gap: 1em;
  &__left{
    max-width: 61.375em;
    p{
      font-weight: 500;
      letter-spacing: 0.02em;
      font-size: 1.1875em;
      line-height: 179%;
    }
  }
  &__btn{
    width: 100%;
    height: 4.25em;
    border-radius: .625em;
    font-weight: 600;
    span{
      letter-spacing: 0.04em;
      font-size: 1em;
    }
  }
}
