.main{
  position: relative;
  @media screen and (min-width: 993px){
    .swiper-slide{
      padding: 11.5% 0;
      &._center{
        .catalog-poster__info{
          margin: 0 auto;
          text-align: center;
        }
        .main__nav{
          justify-content: center;
        }
      }
      &._right{
        .catalog-poster__info{
          margin: 0 6.875em 0 auto;
          text-align: right;
        }
        .main__nav{
          justify-content: flex-end;
        }
      }
      &._black{
        .catalog-poster__info{
          color: $black;
        }
      }
    }
    .catalog-poster__info{
      gap: .625em;
      margin: 0 6.875em;
    }
  }
  .swiper{
    &-button{
      margin: 0;
      background: rgba(255, 255, 255, 0.3);
      opacity: 1;
      &-prev{
        left: 4.375em;
      }
      &-next{
        right: 4.375em;
      }
      svg{
        stroke: #fff;
      }
    }
  }
  .catalog-poster__nav{
    bottom: 50%;
    transform: translateY(50%);
    z-index: 2;
    .container{
      justify-content: space-between;
    }
  }

  .text16{
    line-height: 167%;
  }
  &__nav{
    display: flex;
    margin-top: .625em;
  }
  &__btn{
    font-weight: 700;
    padding: 0 1.875em;
  }
}

.small-catalog{
  background-color: $primary;
  padding: 3.625em 0;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  .header-link{
    flex-direction: column;
    text-align: center;
    height: unset;
    padding: 0;
    &:hover{
      background-color: transparent;
    }
    img{
      margin: 0 0 .625em;
      width: 2.75em;
    }
  }
}

.connect-type{
  padding: 1.625em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__title{
    font-weight: 600;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  }
  &__links{
    margin-left: 1.25em;
    display: flex;
    align-items: center;
    border: 2px solid #D9D9D9;
    border-radius: 3.75em;
    padding: .25em;
  }
  &__link{
    padding: 1.5625em;
    height: 2.5em;
    display: flex;
    align-items: center;
    color: $primary;
    border-radius: 5.875em;
    &:not(:last-child){
      margin-right: .3125em;
    }
    svg{
      width: 1.25em;
      height: 1.25em;
      margin-right: .625em;
      fill: $primary;
    }
    span{
      font-size: .875em;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      font-weight: 600;
    }
    &._active,
    &:hover{
      background-color: $primary;
      color: #fff;
      svg{
        fill: #fff;
      }
    }
  }
}

.config{
  padding: 4.375em 0;
  background-color: $black;
  &__inner{
    display: grid;
    grid-template-columns: 33.8% calc(66.2% - 2.375em);
    justify-content: space-between;
    align-items: center;
  }
  &__right{
    img{
      width: 100%;
      display: block;
      border-radius: .625em;
    }
  }
  &__info{
    @include grid100gap(1em);
  }
  &__subtitle{
    font-weight: 500;
    font-size: 1.5em;
    letter-spacing: 0.02em;
    color: $primary;
  }
  .display2{
    color: #fff;
  }
  .upload-img{
    margin-top: 1.75em;
  }
}

.media{
  padding: 3.125em 0 4.375em;
  background-color: $black;
  color: #fff;
  .display2{
    span{
      color: $primaryHover;
    }
  }
  &__grid{
    margin-top: 1.875em;
  }
  &__grid,
  &__right{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__left{
    .media-card{
      position: relative;
      &__img{
        padding-bottom: 88%;
        min-height: 100%;
      }
      &__content{
        position: absolute;
        background: linear-gradient(180deg, rgba(35, 35, 35, 0) 0%, rgba(35, 35, 35, 0.4) 100%);
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        border-radius: .625em;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        padding: 3.125em;
        margin: 0;
      }
    }
  }
}

.media-card{
  display: block;
  &:hover{
    .media-card__title{
      color: $primary ;
    }
  }
  &__img{
    @include imgCover(58%);
    img{
      border-radius: .625em;
    }
  }
  &__content{
    margin-top: .625em;
  }
  &__title{
    display: flex;
    align-items: center;
    color: #fff;
    font-weight: 600;
    transition: all .3s;
    max-width: 19.375em;
    img{
      width: 2.5em;
      height: 2.5em;
      display: block;
      flex-shrink: 0;
      margin-right: .625em;
    }
    span{
      line-height: 150%;
    }
  }
  &__props{
    margin-top: .625em;
    display: flex;
    align-items: center;
  }
  &__prop{
    display: flex;
    align-items: center;
    color: #6D6D6D;
    line-height: 143%;
    &:not(:last-child){
      margin-right: 1em;
    }
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .375em;
    }
    span{
      font-size: .875em;
    }
  }
}

.guide{
  padding: 3.125em 0 4.375em;
  background-color: $primary;
  .display2{
    color: #fff;
  }
  &__grid{
    display: flex;
    margin-top: 1.875em;
    .fg{
      width: 19.375em;
      flex-shrink: 0;
      margin-right: 1.25em;
    }
  }
  &__btn{
    padding: 0 1.875em;
    height: 4em;
  }
}

.service{
  padding: 3.125em 0 1.5625em;
  &__grid{
    max-width: 1440px;
    margin: 1.875em auto 0;
    display: grid;
    grid-template-columns: repeat(4,25%);
  }
}

.instagram{
  padding-top: 1.5625em;
  &__title{
    display: flex;
    align-items: center;
    img{
      flex-shrink: 0;
      width: 3.25em;
      margin-left: .625em;
      display: block;
    }
  }
  &__grid{
    max-width: 1440px;
    margin: 1.875em auto 0;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6));
  }
}

.instagram-card{
  display: block;
  &__wrap{
    display: block;
    @include imgCover(100%);
  }
}

.collection-banner{
  position: relative;
  overflow: hidden;
  .swiper{
    &-container{
      margin-left: -2px;
      width: calc(200% + 4px);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      background-size: cover;
      background-position: center;
      padding: 2.625em 0 6.25em;
      min-height: 37.0625em;
      height:  auto;
      @media screen and (min-width: 993px){
        &-prev,
        &-next{
          z-index: 2;
          .collection-banner{
            &__info{
              position: relative;
              z-index: 2;
              width: 33%;
              margin-right: 0;
              transform: translateX(100%);
              opacity: 0.4;
              .display2{
                font-size: 1.875em;
                line-height: 180%;
              }
              .text18{
                font-size: .9375em;
              }
            }
            &__nav{
              opacity: 0;
            }
          }
        }
        &-next{
          .collection-banner{
            &__info{
              margin-right: auto;
              margin-left: 0;
              transform: translateX(-100%);
            }
          }
        }
      }
    }
  }
  &__info{
    text-align: center;
    width: 40%;
    margin: 0 auto;
    color: #fff;
    @include grid100gap(.625em);
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1em;
  }
  &__btn{
    font-weight: 700;
    padding: 0 1.875em;
  }
  .catalog-poster__nav{
    bottom: 50%;
    transform: translateY(50%);
    z-index: 2;
    .container{
      position: relative;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    .swiper-slide{
      min-height: 35.5em;
    }
    .catalog-poster__info{
      gap: 1em;
    }
    .display1{
      max-width: calc(100% - 2.1em);
      margin: 0 auto;
    }
    &__nav{
      margin-top: .25em;
      justify-content: center;
    }
  }

  .small-catalog{
    padding: 2.5em 0;
    &__inner{
      margin-top: -1.25em;
      justify-content: center;
      flex-wrap: wrap;
    }
    .header-link{
      width: 29%;
      margin-top: 1.25em;
      span{
        letter-spacing: 0.01em;
      }
      &:nth-child(3n-1){
        width: 42%;
      }
    }
  }

  .connect-type{
    padding: 1.875em 0 1.25em;
    text-align: center;
    &__inner{
      flex-direction: column;
    }
    &__links{
      margin: .9375em 0 0;
    }
  }

  .config{
    display: none;
  }

  .media{
    padding: 3.125em 0 6.25em;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
    &__left{
      .media-card{
        &__content{
          padding: 1.25em;
        }
      }
    }
    &__right{
      @include fullScroll;
      display: flex;
      gap: unset;
      .media-card{
        flex-shrink: 0;
        width: 19.375em;
        &:not(:last-child){
          margin-right: 1.25em;
        }
      }
    }
  }

  .guide{
    padding: 3.75em 0 5em;
    &__grid{
      display: grid;
      grid-template-columns: 100%;
      gap: 1.25em;
      margin-top: 1.25em;
      .fg{
        width: 100%;
        margin: 0;
      }
    }
  }

  .service{
    padding-bottom: 1.875em;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,50%);
    }
  }

  .instagram{
    padding-top: 1.875em;
    &__title{
      img{
        width: 2.625em;
      }
    }
    &__grid{
      display: flex;
      overflow-x: auto;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
  }

  .instagram-card{
    flex-shrink: 0;
    width: 9.375em;
  }

  .collection-banner{
    .catalog-poster__nav{
      display: none;
    }
    &__info{
      width: 100%;
      padding: 0 3em;
      .text18{
        font-size: .9375em;
      }
    }
    &__nav{
      margin-top: .625em;
    }
  }
}
