@import "../mixins";
.error404{
  padding-bottom: 5em;
  &__text{
    @include grid100gap(1.5em);
    max-width: 35em;
  }
  &__desc{
    font-size: 1em;
    line-height: 180%;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    min-width: 17.6875em;
    max-width: 100%;
    height: 4.25em;
    border-radius: .625em;
    font-weight: 600;
    span{
      font-size: 1em;
    }
  }
}
