@import "../mixins";
.materials{
  padding-bottom: 3.125em;
  background: #F7F7F7;
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - (2.5em / 3)));
    gap: 1.25em;
  }
}

.material-card{
  padding: 1em 1.25em 1.5em;
  display: flex;
  flex-direction: column;
  background: #FFFFFF;
  border-radius: .5em;
  &__info{
    @include grid100gap(.875em);
  }
  &__title{
    font-weight: 700;
    font-size: 2.125em;
    line-height: 129%;
    letter-spacing: 0.02em;
  }
  &__desc{
    font-size: 1em;
    line-height: 160%;
    color: #000000;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.5em;
    display: flex;
  }
  &__btn{
    min-width: 12.5em;
    padding: 0 1.5em;
    height: 4.25em;
    border-radius: .625em;
    font-weight: 600;
    span{
      letter-spacing: 0.04em;
      font-size: 1em;
    }
  }
}

.banner{
  padding: 3.75em 0;
  background-color: $black;
  color: #fff;
  &__slider{
    position: relative;
    padding: 0 6.875em;
  }
  .swiper{
    &-container{
      overflow-x: hidden;
    }
    &-slide{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      align-items: center;
      gap: 1.25em;
    }
    &-button{
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
  }
  &__left{
    max-width: 27.5em;
  }
  &__right{
    img{
      width: 100%;
      display: block;
      border-radius: .625em;
    }
  }
  .material-card__nav{
    margin: 0;
    padding-top: 1.25em;
  }
}

.models{
  padding: 3.125em 0 4em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .display1{
    flex-shrink: 0;
  }
  .search-field{
    margin-left: 1em;
    flex-grow: 1;
    max-width: 53.75em;
    input{
      font-size: 1em;
      height: 4.25em;
      border-radius: .625em;
      padding: 0 4em 0 1.875em;
    }
    &__btn{
      right: 1.875em;
    }
    &__variants{
      border-radius: .625em;
    }
  }
  &__grid{
    margin-top: 3.875em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - (11.25em / 4)));
    gap: 3.75em;
  }
}

.product-card2{
  .product-card__nav{
    display: flex;
    gap: unset;
    justify-content: center;
  }
  .product-card__btn{
    padding: 0 1.375em;
  }
}
