@import "../mixins";
.simulate{
  display: grid;
  grid-template-columns: 22.25em calc(100% - 22.25em);
  width: 1440px;
  max-width: 100%;
  margin: 0 auto;
  max-height: 50em;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
    width: 22.25em;
    height: 8.75em;
    left: 0;
    bottom: 0;
    z-index: 2;
  }
  &__sidebar{
    padding: 1.25em 1.875em;
    background-color: #fff;
    overflow-y: auto;
    position: relative;
    max-height: 50em;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }
  &__info{
    @include grid100gap(1.25em);
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.25em 1.5em;
    margin-top: 1.125em;
  }
  &__area{
    position: relative;
  }
  &__img{
    height: 50em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__nav{
    position: absolute;
    bottom: 2.25em;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 47.125em;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__btn{
    width: 16.25em;
    height: 3.0625em;
    border-radius: .25em;
    margin-right: .625em;
    font-weight: 700;
    span{
      font-size: .875em;
    }
  }
  &__socials{
    display: flex;
    align-items: center;
  }
  &__social{
    width: 2.5em;
    flex-shrink: 0;
    &:hover{
      img{
        transform: translateY(-.625em);
      }
    }
    &:not(:last-child){
      margin-right: .875em;
    }
    img{
      width: 100%;
      transition: all .3s;
    }
  }
}

.product-card3{
  padding: 0;
  &._active{
    position: relative;
    &:before{
      content: '';
      @include centerAbsolute;
      width: calc(100% + 1.25em);
      height: calc(100% + 1.25em);
      border: 1px dashed $primary;
      border-radius: .625em;
      pointer-events: none;
    }
  }
  .product-card{
    &__img{
      padding-bottom: 132.8%;
    }
    &__title{
      font-size: .75em;
      margin-top: 1em;
    }
  }
}

.simulate-modal{
  padding: 1.875em;
  z-index: 3;
  position: absolute;
  left: 2.5em;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.49);
  backdrop-filter: blur(4.25em);
  border-radius: 1.25em;
  width: 20.625em;
  .product-card{
    padding: 0;
    color: #fff;
  }
  .m-btn-red-outline,
  .m-btn-gray-outline{
    border: 1px solid #fff;
    color: #fff;
    &:hover{
      background-color: #fff;
      color: $primary;
    }
  }
  .product-card__compare{
    border-color: transparent;
    &:hover{
      background-color: transparent;
      svg{
        fill: $primary;
      }
    }
    svg{
      fill: #fff;
    }
  }
  &__close{
    position: absolute;
    top: .625em;
    right: .625em;
    width: 1.875em;
    height: 1.875em;
    background-color: transparent;
    img{
      width: .6875em;
    }
  }
  &__hint{
    position: absolute;
    left: 100%;
    bottom: 8.125em;
    width: 19.625em;
    margin-left: 1.25em;
    @include grid100gap(.625em);
    color: #fff;
    .display3{
      line-height: 125%;
    }
    .text14{
      line-height: 143%;
    }
  }
  &__arrow{
    position: absolute;
    left: 0;
    top: 100%;
    transform: translate(-1.875em,.625em);
    width: 14.0625em;
  }
}
