@import "../mixins";
.contacts{
  background: #F7F7F7;
  padding-bottom: 3.125em;
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(3,calc((100% / 3) - (2.5em / 3)));
    gap: 1.25em;
  }
}

.contact-card{
  background: #FFFFFF;
  border-radius: .5em;
  padding: .8125em 2.25em 1.625em 1.25em;
  &__title{
    font-weight: 700;
    letter-spacing: 0.02em;
    font-size: 2.125em;
    line-height: 129%;
  }
  &__address{
    font-size: 1em;
    line-height: 160%;
    margin: 1.125em 0 .9375em;
    max-width: 20.25em;
  }
  &__time{
    font-weight: 600;
    font-size: .625em;
    line-height: 150%;
    text-transform: uppercase;
  }
  &__phones{
    margin: 0 auto 1.25em 0;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__links{
    margin-top: 1.75em;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__link{
    display: flex;
    align-items: center;
    font-size: 1em;
    margin-right: auto;
    img{
      width: 1.125em;
      display: block;
      flex-shrink: 0;
      margin-right: .625em;
    }
    &:not(:last-child){
      margin-bottom: .625em;
    }
  }
}

.contacts-map{
  padding: 4.375em 0;
  &__inner{
    position: relative;
  }
}

.contacts-info{
  z-index: 2;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 1.25em;
  background: #FFFFFF;
  box-shadow: 0 .0625em .125em rgba(0, 0, 0, 0.15);
  border-radius: .3125em;
  padding: 1.25em;
  width: 25em;
  @include grid100gap(1.375em)
}

.contact-card2{
  @include grid100gap(.5em);
  &__address{
    line-height: 160%;
    font-size: .875em;
  }
  .contact-card__link{
    margin: .25em 0 0 0;
  }
}

.map{
  height: 31.5em;
  width: 100%;
  border-radius: .625em;
  overflow-x: hidden;
  .ymaps-2-1-79-balloon{
    font-size: 1em;
    font-family: $font1;
    box-shadow: none;
    padding: 0;
    &__tail{
      box-shadow: none;
      background: unset;
      &:after{
        display: none;
      }
    }
    &__layout{
      box-shadow: 0 .25em 1.5em rgba(0, 0, 0, 0.25);
      border-radius: .625em;
      padding: 1.25em;
    }
    &__content{
      font-family: $font1;
      font-size: 1em;
      padding: 0;
      min-width: 18em;
      &>*{
        height: unset !important;
      }
    }
    &__close-button{
      width: 1.875em;
      height: 2.125em;
    }
  }
}

.place-card{
  &__title{
    font-weight: 700;
    font-size: 1.5em;
    line-height: 142%;
    letter-spacing: 0.02em;
  }
  &__address{
    font-size: .875em;
    line-height: 160%;
    margin-top: .78em;
  }
  &__site,
  &__phone,
  &__work{
    display: flex;
    align-items: center;
    margin-top: .625em;

  }
  &__phone{
    img{
      width: 1em;
      margin-right: .625em;
      flex-shrink: 0;
    }
    span{
      font-size: .875em;
    }
  }
  &__site{
    font-weight: 500;
    color: $primary;
    font-size: .875em;
  }
  &__work{
    line-height: 160%;
    font-size: .875em;
    span{
      font-weight: 500;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__grid{
      grid-template-columns: 100%;
      margin-top: 2em;
    }
  }

  .contact-card{
    padding-right: 1.25em;
    &__title{
      font-size: 1.75em;
    }
  }

  .map{
    width: calc(100% + 1.25em);
    margin-left: -.625em;
  }

  .contacts-map{
    padding: 2em 0 0;
  }

  .contacts-info{
    position: static;
    width: 100%;
    transform: unset;
    margin-bottom: 2em;
    padding: 0;
    background-color: transparent;
    box-shadow: none;
  }
}


@media screen and (max-width: 576px){
  .map{
    height: 35.625em;
  }
}
