@import "../mixins";
.collection-poster{
  .catalog-poster__info{
    max-width: 23em;
  }
  .swiper-slide{
    padding: 7.25em 0;
    min-height: 35.3125em;
  }
  &__nav{
    position: absolute;
    bottom: 2.25em;
    left: 0;
    width: 100%;
  }
  &__links{
    display: flex;
  }
  &__link{
    @include center;
    padding: 0 1.875em;
    height: 3.5em;
    font-weight: 600;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    font-size: 1em;
    opacity: .6;
    position: relative;
    margin-right: .9375em;
    color: #fff;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: #fff;
    }
    &:hover,
    &._active{
      opacity: 1;
      color: #fff;
    }
  }
}

.same{
  padding: 1.75em 0 2.5em;
  .breadcrumbs{
    padding: 0;
  }
  .swiper{
    &-slide{
      opacity: 0;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,25%);
    margin-top: 3.125em;
    .same-card{
      border-right: 1px solid #ECECEC;
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4){
        padding-top: 0;
      }
      &:nth-child(4n){
        border-right: none;
      }
    }
  }
}

.same-card{
  padding: 1.75em;
  max-height: 48.75em;
  .swiper{
    &-container{
      position: relative;
      height: 100%;
    }
    &-pagination{
      bottom: unset;
      left: unset;
      right: 0;
      top: 2.5em;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: unset;
      &-bullet{
        margin: 0 0 .625em !important;
        background: #D9D9D9;
        opacity: 1;
        width: .5625em;
        height: .5625em;
        border: 1px solid $primary;
        &-active{

          background: $primary;
        }
      }
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
  }
  .product-card{
    padding: 0;
    height: 100%;
    justify-content: unset;
    &__img{
      img{
        top: unset;
        bottom: 0;
        transform: translate(-50%,0%);
      }
    }
  }
}

.collection-poster2{
  .swiper{
    &-slide{
      padding: 12.5em 0;
    }
    &-button{
      background: #fff;
      svg{
        stroke: $primary;
      }
    }
  }
  .catalog-poster__info{
    max-width: 48.3125em;
  }
}


.collection-advantages{
  padding: 5.75em 0;
}

@media screen and (max-width: 992px){
  .collection-poster{
    .swiper-slide{
      min-height: 35.5em;
      padding: 1.875em 0;
    }
    &__nav{
      .container{
        padding: 0;
      }
    }
    &__links{
      overflow-x: auto;
      padding: 0 .625em;
    }
  }

  .same{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 2em 1.25em;
    }
  }

  .same-card{
    border: none !important;
    padding: 0 !important;
  }
}

@media screen and (max-width: 576px){
  .same{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .same-card{
    .product-card{
      display: block;
      &__img{
        padding: 0 !important;
        img{
          position: static;
          transform: unset;
          max-height: unset;
          width: 100%;
        }
        ._view2{
          display: none;
        }
      }
    }
    .swiper{
      &-container{
        padding-bottom: 2em;
      }
      &-pagination{
        top: unset;
        bottom: 0;
        right: 0;
        width: 100%;
        flex-direction: row;
        justify-content: center;
        &-bullet{
          margin: 0 .5em !important;
        }
      }
    }
  }
}
