@import "../mixins";
.news{
  padding-bottom: 6.25em;
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.875em 1.25em;
  }
}

.news-card{
  @media screen and (min-width: 993px) {
    &._main-news{
      position: relative;
      grid-column: 1/-1;
      color: #fff;
      .news-card{
        &__img{
          padding-bottom: 47%;
        }
        &__content{
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
          border-radius: .625em;
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
          padding: 3.75em;
          margin: 0;
        }
        &__tag{
          color: #fff;
          border-color: #fff;
        }
        &__info{
          max-width: 40em;
          gap: 1.25em;
        }
        &__title{
          font-size: 3em;
          line-height: 112%;
        }
        &__text{
          color: #fff;
          font-size: 1.125em;
          line-height: 150%;
        }
      }
    }
    &._big-news {
      grid-column: span 2;
      grid-row: span 2;

      .news-card {
        &__title {
          font-size: 1.75em;
          line-height: 121%;
          max-width: 13em;
        }

        &__info {
          max-width: 36.25em;
        }
      }
    }
  }
  &__img{
    @include imgCover(58%);
    display: block;
    img{
      border-radius: .625em;
    }
  }
  &__content{
    margin-top: 1.125em;
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__date,
  &__tag{
    @include center;
    padding: 0 .3125em;
    border-radius: .375em;
    height: 1.375em;
    font-weight: 600;
    margin-right: .625em;
    span{
      font-size: .75em;
    }
  }
  &__date{
    background-color: $primary;
    color: #fff;
  }
  &__tag{
    border: 1px solid #DFDFDF;
    &:hover{
      border-color: $primary;
    }
  }
  &__info{
    @include grid100gap(.625em);
    margin-top: .625em;
    max-width: 17.5em;
  }
  &__title{
    font-weight: 600;
  }
  &__text{
    line-height: 143%;
    color: #6D6D6D;
  }
}

.subscribe{
  padding: 1.5em 1.875em 1.875em;
  background-color: $primary;
  color: #FFFFFF;
  border-radius: .625em;
  display: flex;
  flex-direction: column;
  &__title{
    font-size: 1.75em;
    line-height: 121%;
    font-weight: 600;
    max-width: 7.5em;
  }
  &__form{
    @include grid100gap(.625em);
    margin-top: 1.25em;
    .fg{
      input{
        background-color: transparent;
        border-color: #fff;
        color: #FFFFFF;
        text-transform: unset;
        height: 3.43em;
        border-radius: .3125em;
        font-weight: 500;
        &::placeholder{
          color: #FFFFFF;
        }
      }
    }
  }
  &__btn{
    font-weight: 500;
    text-transform: unset;
    border-radius: .3125em;
  }
  &__socials{
    padding-top: 2em;
    margin-top: auto;
    display: flex;
    align-items: center;
  }
  &__social{
    flex-shrink: 0;
    width: 2.5em;
    height: 2.5em;
    @include center;
    margin-right: .375em;
    border-radius: 50%;
    background: #A70026;
    &:hover{
      background: $primaryHover;
    }
    img{
      width: 1.25em;
    }
  }
}

.news-page{
  background-size: cover;
  background-position: center;
  padding: 17.75em 0 8.3125em;
  color: #fff;
  &__info{
    margin: 0 auto;
    max-width: 53.625em;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    .display1{
      text-align: center;
    }
  }
  &:not(.news-page2){
    .breadcrumbs{
      padding: 0 0 .625em;
      &__item{
        color: #fff;
        &:last-child{
          color: #fff;
        }
        &:not(:first-child) {
          &:before {
            background-image: url(../img/bread-arrow2.svg);
          }
        }
      }
    }
  }

}

.news-page2{
  color: $black;
  padding: 0 0 8.125em;
}

.article{
  padding-bottom: 5em;
  &__inner{
    margin: -4.25em auto 0;
    padding: 2.875em 3.75em 3.75em;
    background: #FFFFFF;
    box-shadow: 0 .25em 1em rgba(0, 0, 0, 0.05);
    border-radius: 1.25em;
    width: 100%;
    max-width: 67.5em;
  }

  &__grid{
    &:not(:last-child){
      margin-bottom: 1.875em;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    align-items: center;
    gap: 1.25em;
    p,img{
      margin: 0 !important;
    }
  }
}

.article-text{
  h3{
    font-weight: 700;
    font-size: 2.125em;
    line-height: 188%;
    letter-spacing: 0.02em;
    &:not(:last-child){
      margin-bottom: .58em;
    }
  }
  p,
  li{
    font-size: 1em;
    line-height: 160%;
  }
  img,
  ol,ul,
  &__grid,
  p{
    &:not(:last-child){
      margin-bottom: 1.875em;
    }
  }
  li{
    &:not(:last-child){
      margin-bottom: .9375em;
    }
  }
  ol{
    li{
      display: flex;
      &:before{
        content: attr(data-num);
        font-weight: 600;
        color: $primary;
        order: -1;
      }
      &:after{
        content: '/';
        order: -1;
        color: #E8E8E8;
        font-weight: 600;
        margin: 0 .8em 0 .5em;
      }
    }
  }
  img{
    border-radius: .625em;
    display: block;
    width: 100%;
  }
}


@media screen and (max-width: 992px){
  .news{
    padding-bottom: 4em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .news-page{
    padding-bottom: 2em;
    .display1{
      font-size: 1.5em;
      line-height: 130%;
    }
  }

  .news-page2{
    padding-bottom: 0;
  }

  .article{
    padding: 2em 0 4em;
    &__inner{
      margin: 0;
      padding: 0;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .article-text{
    h3{
      font-size: 1.25em;
    }
    p,li{
      font-size: .875em;
    }
  }
}
