.catalog2{
  padding-bottom: 6.25em;
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
  }
}

.catalog-card{
  padding: 4.125em 1em;
  background: #F4F4F4;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__icon{
    height: 2.75em;
    width: auto;
    flex-shrink: 0;
    display: block;
    margin-bottom: 1.25em;
  }
  &__title{
    font-weight: 600;
    font-size: .875em;
    line-height: 160%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
  }
}



@media screen and (max-width: 992px){
  .catalog2{
    &__grid{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }
}


@media screen and (max-width: 576px){
  .catalog2{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
  }

  .catalog-card{
    padding: 1.5em .5em;
    &__icon{
      height: 2.125em;
    }
    &__title{
      font-size: .75em;
    }
  }
}
